/* Kategorie nav */

nav.co-category-list-container {
  background: #FFF;
  padding: 0 30px 20px 30px;
  margin-bottom: 1px;
  min-width:300px;
}

nav.co-category-list-container h3 {
  padding: 20px 30px;
  margin: 0 -30px;
  border-bottom: 1px solid #efefef;
  color: #333;
  font-size: 18px;
  font-weight: 600;
}

nav.co-category-list-container ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  margin-top: 10px;
}

nav.co-category-list-container ul li {
  position: relative;
}

nav.co-category-list-container ul li a:link,
nav.co-category-list-container ul li a:visited {
  padding: 10px 0;
  font-size: 18px;
  display: block;
  color: #333;
  transition: color .3s linear;
}

nav.co-category-list-container ul li a:hover,
nav.co-category-list-container ul li a:focus {
  color: #0088F4;
}

nav.co-category-list-container ul li span {
