/* Prev Next Navigation */

.app-blog .app-blog-previouslink,
.app-blog .app-blog-nextlink {
  overflow: hidden;
  background: #efefef;
  position: relative;
  margin-bottom:1px;
}

.app-blog .app-blog-previouslink:hover,
.app-blog .app-blog-nextlink:hover {
  
  background: #FFF;
  position: relative;
  margin-bottom:1px;
}


.app-blog-nextlink:after {
    content: "\f061";
    font-family: FontAwesome;
    position: absolute;
    right: 10px;
    top:0;bottom:0;
    padding-left:8px;
    line-height:74px;
    border-left:1px #FFF solid;
    font-size: 18px;
    color: #0088F4;
    vertical-align: top;
}
.app-blog-previouslink:hover:after,
.app-blog-nextlink:hover:before {
  border-color:#efefef;
}

.app-blog-previouslink:before {
  content: "\f060";
  font-family: FontAwesome;
  position: absolute;
  left: 10px;
  top:0;bottom:0;
  padding-right:8px;
  line-height:74px;
  border-right:1px #FFF solid;
  font-size: 18px;
  color: #0088F4;
  vertical-align: top;
}

.app-blog-previouslink img {
  float: right;
}
.app-blog-nextlink img {
  float: left;
}

.app-blog-nextlink span {
  float: left;
  display: block;
  padding: 10px 10px 10px 15px;
  max-width: 70%;
  font-size: 14px;
  line-height: 18px;
}
.app-blog-previouslink span {
  float: right;
  display: block;
  padding: 10px 15px 10px 10px;
  max-width: 70%;
  font-size: 14px;
  line-height: 18px;
}
.app-blog-nextlink:hover span,
.app-blog-previouslink:hover span
 {color:#333!important;}

